.list-group-item-heading {
  margin-top: 0;
  margin-bottom: 5px;
}

.list-group-item-text {
  margin-bottom: 0;
  line-height: 1.3;
}

.list-group {
  border-radius: 10px;
}

a.list-group-item {
  font-weight: 300;
}

.list-group-item a {
  display: block;
}

.list-group-item-heading {
  margin: 0;
  line-height: inherit;
}

.list-group-item .icon-small,
.list-group-item:focus .icon-small {
  fill: #4d4d4d;
  margin-right: .25em;
}

.list-group-item.active .icon-small,
.list-group-item:hover .icon-small,
.list-group-item.active:hover .icon-small,
.list-group-item.active:focus .icon-small {
  fill: white;
}

.list-group-divider .list-group-item {
  border: 0;
  line-height: 1.2;
}

.list-group-divider .list-group-item .media-left {
  padding-right: 10px;
}

.card-body .list-group {
  margin-bottom: 0;
}

.card-body .list-group .list-group-item {
  padding-left: 0;
  padding-right: 0;
}

.list-group-item.media {
  margin-top: 0;
}

.list-group .avatar {
  min-height: 62px;
  padding-left: 72px;
  position: relative;
}

.list-group .avatar .circle {
  position: absolute;
  width: 42px;
  height: 42px;
  overflow: hidden;
  left: 15px;
  display: inline-block;
  vertical-align: middle;
  border-radius: 50%;
}

.list-group-label {
  display: inline-block;
  margin-right: 20px;
  width: 180px;
  font-weight: 500;
  vertical-align: top;
}

.list-group-field {
  display: inline-block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
}

.card-body .list-group-item {
  border-left: 0;
  border-right: 0;
}

@media screen and (min-width: 768px) {

  .card-body .list-group .list-group-item:not(.media) {
    display: table;
    table-layout: fixed;
    width: 100%;
  }

  .card-body .list-group .list-group-label,
  .card-body .list-group .list-group-field {
    display: table-cell;
  }

  .card-body .list-group .list-group-field {
    max-width: 70%;
  }
}
